import React, { Component } from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import { v4 as uuidv4 } from 'uuid';
import RemoveIcon from '@material-ui/icons/Remove';
import CloseIcon from '@material-ui/icons/Close';
import { NavLink } from 'react-router-dom';
import {
  API_ROOT,
  HTTP_ORIGIN,
  AJAX_REQUEST_TYPES,
  FLASH_MESSAGE_TYPES
} from '../../constants/constants';
import {
  UPDATE_SELECTION,
  ADD_PENDING_REQUEST,
  FINISH_PENDING_REQUEST,
  ADD_FLASH_MESSAGE
} from '../../actions/index.actions';

interface TableProps {
  userId: string;
  addPendingRequest: Function;
  finishPendingRequest: Function;
  addFlashMessage: Function;
}

interface TableState {
  trackList: any;
  filtered: boolean;
  key: string | null;
  mode: string | null;
  sort: any;
  errorFetching: boolean;
  disableTable: boolean;
  query: string;
  trackSelection: any;
}

const SortButton: any = (
  column: any,
  selectedColumn: any,
  direction: any,
  sortColumn: Function
): any =>
  column === selectedColumn ? (
    <th onClick={e => sortColumn(e)}>
      {column}
      {direction === 0 || !direction ? <>&#8593;&#8595;</> : null}
      {direction === 1 ? <>&#8593;</> : null}
      {direction === 2 ? <>&#8595;</> : null}
    </th>
  ) : (
    <th
      onClick={e => {
        sortColumn(e);
      }}
    >
      {column}&#8593;&#8595;
    </th>
  );

class SelectionTable extends Component<TableProps, TableState> {
  constructor(props: TableProps) {
    super(props);

    const storedSelection: any = localStorage.getItem('tonalify_selection');
    const parsedSelection: any = JSON.parse(storedSelection);

    this.state = {
      trackList: [],
      filtered: false,
      key: null,
      mode: null,
      sort: { column: null, direction: null },
      errorFetching: false,
      disableTable: false,
      query: '',
      trackSelection: parsedSelection ? parsedSelection : []
    };
  }

  filterByKey(e: any) {
    if (e.target.name === 'key') {
      this.setState({
        key: e.target.value,
        filtered: !!e.target.value || !!this.state.mode
      });

      return;
    }

    if (e.target.name === 'mode') {
      this.setState({
        mode: e.target.value,
        filtered: !!e.target.value || !!this.state.key
      });
    }
  }

  getFilteredTracks() {
    const { key, mode } = this.state;
    const filtered = [...this.state.trackSelection].filter((track: any) => {
      if (key && mode && track.key === key && track.mode === mode) {
        return track;
      }
      if (key && !mode && track.key === key) {
        return track;
      }
      if (!key && mode && track.mode === mode) {
        return track;
      }

      if (!key && !mode) {
        return track;
      }
    });

    if (!this.state.query) {
      return filtered;
    }

    return filtered.filter((track: any) => {
      const { name, artist, tempo, mode, key } = track;
      let passesFilter = false;
      [name, artist, tempo, mode, key].map((property: any) => {
        if (
          property &&
          property
            .toString()
            .toLowerCase()
            .includes(this.state.query.toLocaleLowerCase())
        ) {
          passesFilter = true;
        }
      });

      if (passesFilter) return track;
    });
  }

  search(e: any) {
    if (e.target.value && e.target.value.length) {
      this.setState({ query: e.target.value, filtered: true });
      return;
    }

    this.setState({ query: '', filtered: false });
  }

  removeFromSelection(track: any) {
    const selectionString: any = localStorage.getItem('tonalify_selection');
    const oldSelection: any = JSON.parse(selectionString)
      ? JSON.parse(selectionString)
      : [];

    const updatedSelection = oldSelection.filter(
      (cachedTrack: any) => cachedTrack.id !== track.id
    );

    localStorage.setItem(
      'tonalify_selection',
      JSON.stringify(updatedSelection)
    );
    this.setState({ trackSelection: updatedSelection });
  }

  async exportSelection(e: any) {
    e.preventDefault();
    const requestId = uuidv4();
    const updatePlaylistRequestId = uuidv4();
    const newPlaylistName = e.target.playlist_name.value;
    const newPlaylistDescription = e.target.playlist_description.value;
    try {
      this.props.addPendingRequest({
        id: requestId,
        type: AJAX_REQUEST_TYPES.USER_LIBRARY,
        message: 'Creating New Playlist'
      });
      const exportedSelection = await axios.post(
        `${API_ROOT}/spotify-request`,
        {
          spotify_path: `https://api.spotify.com/v1/users/${this.props.userId}/playlists`,
          spotify_payload: {
            name: newPlaylistName,
            description: newPlaylistDescription
          },
          crud_operation: 'post'
        },
        {
          withCredentials: true,
          headers: { 'Access-Control-Allow-Origin': HTTP_ORIGIN }
        }
      );

      this.props.finishPendingRequest(requestId);

      if (
        !exportedSelection ||
        !exportedSelection.data ||
        !exportedSelection.data.id
      ) {
        return;
      }

      this.props.addPendingRequest({
        id: updatePlaylistRequestId,
        type: AJAX_REQUEST_TYPES.USER_LIBRARY,
        message: 'Adding Tracks To Playlist'
      });

      const updatedPlaylist = await axios.post(
        `${API_ROOT}/spotify-request`,
        {
          spotify_path: `https://api.spotify.com/v1/playlists/${exportedSelection.data.id}/tracks`,
          spotify_payload: {
            uris: this.state.trackSelection.map((track: any) => track.uri)
          },
          crud_operation: 'post'
        },
        {
          withCredentials: true,
          headers: { 'Access-Control-Allow-Origin': HTTP_ORIGIN }
        }
      );

      if (
        !updatedPlaylist ||
        !updatedPlaylist.data ||
        !updatedPlaylist.data.snapshot_id
      )
        return;

      this.props.finishPendingRequest(updatePlaylistRequestId);

      this.props.addFlashMessage({
        type: FLASH_MESSAGE_TYPES.SUCCESS,
        text: `Created ${newPlaylistName}`,
        id: uuidv4()
      });
    } catch (error) {
      this.props.finishPendingRequest(updatePlaylistRequestId);
      this.props.finishPendingRequest(requestId);
      this.props.addFlashMessage({
        type: FLASH_MESSAGE_TYPES.ERROR,
        text: 'Error Creating Playlist',
        id: uuidv4()
      });
    }
  }

  sortColumn(column: string) {
    let direction = 1;
    if (this.state.sort.column !== column) {
      direction = 1;
    } else {
      switch (this.state.sort.direction) {
        case 0:
          direction = 1;
          break;
        case 1:
          direction = 2;
          break;
        case 2:
          direction = 0;
          break;
      }
    }
    this.setState({
      sort: { column, direction }
    });
  }

  getSortedTracks(filteredTracks: any) {
    const { sort } = this.state;
    const { direction, column } = sort;
    return [...filteredTracks].sort((first: any, second: any) => {
      if (direction === 1) return first[column] > second[column] ? 1 : -1;
      if (direction === 2) return first[column] < second[column] ? 1 : -1;
      return 1;
    });
  }

  render() {
    const keys = [
      'C',
      'C#',
      'D',
      'D#',
      'E',
      'F',
      'F#',
      'G',
      'G#',
      'A',
      'A#',
      'B'
    ];

    const tracks = this.state.filtered
      ? this.getFilteredTracks()
      : this.state.trackSelection;

    const sortedTracks = this.getSortedTracks(tracks);

    return (
      <div className="user-selection-wrapper">
        {!this.state.errorFetching ? (
          <>
            <div className="section">
              <div className="container has-text-centered is-widescreen">
                <p className="title">Export To Spotify</p>
                <p>
                  Modify and export your current selection or return to your{' '}
                  <NavLink to="/your-playlists">Playlists</NavLink> and{' '}
                  <NavLink to="/your-albums">Albums</NavLink> to continue adding
                  tracks
                </p>
              </div>
            </div>
            <div className="section">
              <div className="columns is-centered">
                <div className="form-wrapper box column is-half">
                  <div className="has-text-centered">
                    <p>Export Playlist</p>
                  </div>

                  <form
                    id="playlist-form"
                    className="create-playlist-form"
                    onSubmit={e => this.exportSelection(e)}
                  >
                    <div className="playlist-inputs">
                      <div className="field">
                        <label htmlFor="name" className="label">
                          Name
                        </label>
                        <input
                          id="name"
                          className="input"
                          name="playlist_name"
                          placeholder="Playlist Name"
                        ></input>
                      </div>
                      <div className="field">
                        <label htmlFor="description" className="label">
                          Description
                        </label>
                        <textarea
                          id="description"
                          rows={2}
                          className="playlist-description textarea"
                          name="playlist_description"
                          placeholder="Playlist Description"
                        ></textarea>
                      </div>
                    </div>
                    <div className="export-button-wrapper">
                      <button className="export-button button is-primary">
                        Create
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>

            <div className="table-section-wrapper">
              <div className="table-wrapper">
                {this.state.disableTable ? (
                  <div className="table-ajax-cover">
                    <div className="ajax-spinner">
                      <div className="spinner"></div>
                    </div>
                  </div>
                ) : null}
                <div className="table-controls-wrapper">
                  <div className="key-and-mode-filter-wrapper">
                    <form
                      onChange={e => {
                        this.filterByKey(e);
                      }}
                    >
                      <div className="filter-wrapper">
                        <div className="select">
                          <select name="key">
                            <option
                              selected={!this.state.key}
                              disabled
                              value=""
                            >
                              Key
                            </option>
                            {keys.map(key => (
                              <option value={key}>{key}</option>
                            ))}
                          </select>
                        </div>
                        {this.state.key ? (
                          <button onClick={e => this.setState({ key: null })}>
                            <CloseIcon className="has-text-link" />
                          </button>
                        ) : null}
                      </div>
                      <div className="filter-wrapper">
                        <div className="select">
                          <select name="mode">
                            <option
                              selected={!this.state.mode}
                              disabled
                              value=""
                            >
                              mode
                            </option>
                            <option value="major">major</option>
                            <option value="minor">minor</option>
                          </select>
                        </div>
                        {this.state.mode ? (
                          <button onClick={e => this.setState({ mode: null })}>
                            <CloseIcon className="has-text-link" />
                          </button>
                        ) : null}
                      </div>
                    </form>
                  </div>
                  <div className="search-input-wrapper field">
                    <input
                      className="input is-small"
                      onChange={e => this.search(e)}
                      placeholder={'Search'}
                      value={this.state.query}
                    ></input>
                    {this.state.query.length ? (
                      <button
                        onClick={() => {
                          this.setState({ query: '' });
                        }}
                      >
                        <CloseIcon className="has-text-link" />
                      </button>
                    ) : null}{' '}
                  </div>
                </div>

                <table className="table is-bordered is-striped ">
                  <thead>
                    <tr>
                      <th>deselect</th>

                      {SortButton(
                        'name',
                        this.state.sort.column,
                        this.state.sort.direction,
                        (e: any) => {
                          e.preventDefault();
                          this.sortColumn('name');
                        }
                      )}

                      {SortButton(
                        'key',
                        this.state.sort.column,
                        this.state.sort.direction,
                        (e: any) => {
                          e.preventDefault();
                          this.sortColumn('key');
                        }
                      )}

                      {SortButton(
                        'mode',
                        this.state.sort.column,
                        this.state.sort.direction,
                        (e: any) => {
                          e.preventDefault();
                          this.sortColumn('mode');
                        }
                      )}

                      {SortButton(
                        'tempo',
                        this.state.sort.column,
                        this.state.sort.direction,
                        (e: any) => {
                          e.preventDefault();
                          this.sortColumn('tempo');
                        }
                      )}

                      {SortButton(
                        'artist',
                        this.state.sort.column,
                        this.state.sort.direction,
                        (e: any) => {
                          e.preventDefault();
                          this.sortColumn('artist');
                        }
                      )}
                    </tr>
                  </thead>
                  <tbody>
                    {sortedTracks.map((row: any) => (
                      <tr>
                        <td align="center">
                          <RemoveIcon
                            className={`selection-button-icon has-text-link ${
                              this.state.disableTable ? 'disabled-icon' : ''
                            }`}
                            onClick={() => {
                              this.removeFromSelection(row);
                            }}
                          />
                        </td>
                        <td>{row.name}</td>
                        <td>{row.key}</td>
                        <td>{row.mode}</td>
                        <td>{Number(row.tempo)}</td>
                        <td>{row.artist}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          </>
        ) : (
          <p className="notification is-danger">
            Error Loading Track Selection, Refresh the Page
          </p>
        )}
      </div>
    );
  }
}

function mapStateToProps(state: any) {
  return { userId: state.auth.id };
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    addFlashMessage: (message: any) =>
      dispatch({ type: ADD_FLASH_MESSAGE, payload: message }),
    addPendingRequest: (request: any) =>
      dispatch({ type: ADD_PENDING_REQUEST, payload: request }),
    finishPendingRequest: (id: any) =>
      dispatch({ type: FINISH_PENDING_REQUEST, payload: id })
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SelectionTable);
