import React, { Component } from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import { v4 as uuidv4 } from 'uuid';
import { NavLink } from 'react-router-dom';
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';
import CloseIcon from '@material-ui/icons/Close';
import {
  API_ROOT,
  HTTP_ORIGIN,
  AJAX_REQUEST_TYPES,
  FLASH_MESSAGE_TYPES
} from '../../constants/constants';
import {
  UPDATE_SELECTION,
  ADD_FLASH_MESSAGE,
  ADD_PENDING_REQUEST,
  FINISH_PENDING_REQUEST
} from '../../actions/index.actions';

const keysMap: any = {
  0: 'C',
  1: 'C#',
  2: 'D',
  3: 'D#',
  4: 'E',
  5: 'F',
  6: 'F#',
  7: 'G',
  8: 'G#',
  9: 'A',
  10: 'A#',
  11: 'B'
};

const modesMap: any = {
  0: 'minor',
  1: 'major'
};

interface TableProps {
  addPendingRequest: Function;
  finishPendingRequest: Function;
  addFlashMessage: Function;
  spotifyPath: string | null;
}

interface TableState {
  trackList: any;
  spotifyPath: string | null;
  filtered: boolean;
  key: string | null;
  mode: string | null;
  sort: any;
  errorFetchingTracks: boolean;
  disableTable: boolean;
  query: string;
  trackSelection: any;
}

const SortButton: any = (
  column: any,
  selectedColumn: any,
  direction: any,
  sortColumn: Function
): any =>
  column === selectedColumn ? (
    <th onClick={e => sortColumn(e)}>
      {column}
      {direction === 0 || !direction ? <>&#8593;&#8595;</> : null}
      {direction === 1 ? <>&#8593;</> : null}
      {direction === 2 ? <>&#8595;</> : null}
    </th>
  ) : (
    <th
      onClick={e => {
        sortColumn(e);
      }}
    >
      {column}&#8593;&#8595;
    </th>
  );

class Table extends Component<TableProps, TableState> {
  constructor(props: TableProps) {
    super(props);

    const storedSelection: any = localStorage.getItem('tonalify_selection');
    const parsedSelection: any = JSON.parse(storedSelection);

    this.state = {
      trackList: [],
      spotifyPath: new URLSearchParams(window.location.search).get(
        'spotify_path'
      ),
      filtered: false,
      key: null,
      mode: null,
      sort: { column: null, direction: null },
      errorFetchingTracks: false,
      disableTable: false,
      query: '',
      trackSelection: parsedSelection ? parsedSelection : []
    };

    this.getTracks();
  }

  async getTracks() {
    const requestId = uuidv4();
    const featuresRequestId = uuidv4();
    try {
      this.props.addPendingRequest({
        id: requestId,
        type: AJAX_REQUEST_TYPES.USER_LIBRARY,
        message: 'Fetching Tracks'
      });

      const tracks = await axios.post(
        `${API_ROOT}/spotify-request`,
        {
          spotify_path: this.state.spotifyPath,
          spotify_payload: null,
          crud_operation: 'get'
        },
        {
          withCredentials: true,
          headers: { 'Access-Control-Allow-Origin': HTTP_ORIGIN }
        }
      );

      this.props.finishPendingRequest(requestId);

      if (!tracks.data || !tracks.data.items) return;

      const { items, next } = tracks.data;

      this.props.addPendingRequest({
        id: featuresRequestId,
        type: AJAX_REQUEST_TYPES.USER_LIBRARY,
        message: 'Fetching Audio Details'
      });

      const tracksFeaturesResponse = await axios.post(
        `${API_ROOT}/spotify-request`,
        {
          spotify_path: `https://api.spotify.com/v1/audio-features?ids=${items.map(
            (item: any) => {
              if (item.track) {
                item = item.track;
              }
              return item.id;
            }
          )}`,
          spotify_payload: null,
          crud_operation: 'get'
        },
        {
          withCredentials: true,
          headers: { 'Access-Control-Allow-Origin': HTTP_ORIGIN }
        }
      );

      this.props.finishPendingRequest(featuresRequestId);

      let tracksFeatures: [] = [];

      if (
        tracksFeaturesResponse &&
        tracksFeaturesResponse.data &&
        tracksFeaturesResponse.data.audio_features
      ) {
        tracksFeatures = tracksFeaturesResponse.data.audio_features;
      }

      const updatedTrackList = [...this.state.trackList].concat(
        items.map((item: any) => {
          if (item.track) {
            item = item.track;
          }
          const features: any | undefined = tracksFeatures.find(
            (track: any) => track && item && track.id === item.id
          );

          return {
            ...item,
            selected: false,
            tempo: features ? features.tempo : null,
            key: features ? keysMap[features.key] : null,
            mode: features ? modesMap[features.mode] : null,
            artist:
              item.artists && item.artists.length ? item.artists[0].name : null
          };
        })
      );

      this.setState(
        {
          ...this.state,
          trackList: updatedTrackList,
          spotifyPath: next,
          errorFetchingTracks: false
        },
        () => {
          if (!next) return;
          this.getTracks();
        }
      );
    } catch (error) {
      this.props.finishPendingRequest(requestId);
      this.props.finishPendingRequest(featuresRequestId);
      this.setState({
        ...this.state,
        errorFetchingTracks: true
      });
    }
  }

  filterByKey(e: any) {
    if (e.target.name === 'key') {
      this.setState({
        key: e.target.value,
        filtered: !!e.target.value || !!this.state.mode
      });

      return;
    }

    if (e.target.name === 'mode') {
      this.setState({
        mode: e.target.value,
        filtered: !!e.target.value || !!this.state.key
      });
    }
  }

  search(e: any) {
    if (e.target.value && e.target.value.length) {
      this.setState({ query: e.target.value, filtered: true });
      return;
    }

    this.setState({ query: '', filtered: false });
  }

  getFilteredTracks() {
    const { key, mode } = this.state;
    const filtered = [...this.state.trackList].filter((track: any) => {
      if (key && mode && track.key === key && track.mode === mode) {
        return track;
      }
      if (key && !mode && track.key === key) {
        return track;
      }
      if (!key && mode && track.mode === mode) {
        return track;
      }

      if (!key && !mode) {
        return track;
      }
    });

    if (!this.state.query) {
      return filtered;
    }

    return filtered.filter((track: any) => {
      const { name, artist, tempo, mode, key } = track;
      let passesFilter = false;
      [name, artist, tempo, mode, key].map((property: any) => {
        if (
          property &&
          property
            .toString()
            .toLowerCase()
            .includes(this.state.query.toLocaleLowerCase())
        ) {
          passesFilter = true;
        }
      });

      if (passesFilter) return track;
    });
  }

  addToSelection(track: any) {
    const selectionString: any = localStorage.getItem('tonalify_selection');
    const oldSelection = JSON.parse(selectionString)
      ? JSON.parse(selectionString)
      : [];
    const updatedSelection = [...oldSelection, track];
    localStorage.setItem(
      'tonalify_selection',
      JSON.stringify(updatedSelection)
    );

    this.setState({ trackSelection: updatedSelection });
  }

  removeFromSelection(track: any) {
    const selectionString: any = localStorage.getItem('tonalify_selection');
    const oldSelection: any = JSON.parse(selectionString)
      ? JSON.parse(selectionString)
      : [];

    const updatedSelection = oldSelection.filter(
      (cachedTrack: any) => cachedTrack.id !== track.id
    );

    localStorage.setItem(
      'tonalify_selection',
      JSON.stringify(updatedSelection)
    );
    this.setState({ trackSelection: updatedSelection });
  }

  sortColumn(column: string) {
    let direction = 1;
    if (this.state.sort.column !== column) {
      direction = 1;
    } else {
      switch (this.state.sort.direction) {
        case 0:
          direction = 1;
          break;
        case 1:
          direction = 2;
          break;
        case 2:
          direction = 0;
          break;
      }
    }
    this.setState({
      sort: { column, direction }
    });
  }

  getSortedTracks(filteredTracks: any) {
    const { sort } = this.state;
    const { direction, column } = sort;
    return [...filteredTracks].sort((first: any, second: any) => {
      if (direction === 1) return first[column] > second[column] ? 1 : -1;
      if (direction === 2) return first[column] < second[column] ? 1 : -1;
      return 1;
    });
  }

  render() {
    const keys = [
      'C',
      'C#',
      'D',
      'D#',
      'E',
      'F',
      'F#',
      'G',
      'G#',
      'A',
      'A#',
      'B'
    ];

    const tracks = this.state.filtered
      ? this.getFilteredTracks()
      : this.state.trackList;

    const sortedTracks = this.getSortedTracks(tracks);

    const title = new URLSearchParams(window.location.search).get('title');

    return (
      <div className="tracks-view-wrapper">
        {!this.state.errorFetchingTracks ? (
          <div className="tracks-view-content">
            <div className="section">
              <div className="container has-text-centered is-widescreen">
                <p className="title">{title}</p>
                <p>
                  This won't modify {title} or your library, selecting and
                  deselecting tracks only modifies your selection of tracks in
                  this app. Use the table below to select and deselect tracks.{' '}
                  <NavLink to="/selection">Export</NavLink> to modify and export
                  your full selection of tracks, or continue to{' '}
                  <NavLink to="/your-playlists">Playlists</NavLink> and{' '}
                  <NavLink to="/your-albums">Albums</NavLink> to continue adding
                  tracks{' '}
                </p>
              </div>
            </div>
            <div className="table-section-wrapper">
              <div className="table-wrapper">
                {this.state.disableTable ? (
                  <div className="table-ajax-cover">
                    <div className="ajax-spinner">
                      <div className="spinner"></div>
                    </div>
                  </div>
                ) : null}
                <div className="table-controls-wrapper">
                  <div className="key-and-mode-filter-wrapper">
                    <form
                      onChange={e => {
                        this.filterByKey(e);
                      }}
                    >
                      <div className="filter-wrapper">
                        <div className="select">
                          <select name="key">
                            <option
                              selected={!this.state.key}
                              disabled
                              value=""
                            >
                              Key
                            </option>
                            {keys.map(key => (
                              <option value={key}>{key}</option>
                            ))}
                          </select>
                        </div>
                        {this.state.key ? (
                          <button onClick={e => this.setState({ key: null })}>
                            <CloseIcon className="has-text-link" />
                          </button>
                        ) : null}
                      </div>
                      <div className="filter-wrapper">
                        <div className="select">
                          <select name="mode">
                            <option
                              selected={!this.state.mode}
                              disabled
                              value=""
                            >
                              mode
                            </option>
                            <option value="major">major</option>
                            <option value="minor">minor</option>
                          </select>
                        </div>
                        {this.state.mode ? (
                          <button onClick={e => this.setState({ mode: null })}>
                            <CloseIcon className="has-text-link" />
                          </button>
                        ) : null}
                      </div>
                    </form>
                  </div>
                  <div className="search-input-wrapper">
                    <input
                      className="input is-small"
                      onChange={e => this.search(e)}
                      placeholder={'Search'}
                      value={this.state.query}
                    ></input>
                    {this.state.query.length ? (
                      <button
                        onClick={() => {
                          this.setState({ query: '' });
                        }}
                      >
                        <CloseIcon className="has-text-link" />
                      </button>
                    ) : null}
                  </div>
                </div>

                <table className="table is-bordered is-striped">
                  <thead>
                    <tr>
                      <th>select</th>

                      {SortButton(
                        'name',
                        this.state.sort.column,
                        this.state.sort.direction,
                        (e: any) => {
                          e.preventDefault();
                          this.sortColumn('name');
                        }
                      )}

                      {SortButton(
                        'key',
                        this.state.sort.column,
                        this.state.sort.direction,
                        (e: any) => {
                          e.preventDefault();
                          this.sortColumn('key');
                        }
                      )}

                      {SortButton(
                        'mode',
                        this.state.sort.column,
                        this.state.sort.direction,
                        (e: any) => {
                          e.preventDefault();
                          this.sortColumn('mode');
                        }
                      )}

                      {SortButton(
                        'tempo',
                        this.state.sort.column,
                        this.state.sort.direction,
                        (e: any) => {
                          e.preventDefault();
                          this.sortColumn('tempo');
                        }
                      )}

                      {SortButton(
                        'artist',
                        this.state.sort.column,
                        this.state.sort.direction,
                        (e: any) => {
                          e.preventDefault();
                          this.sortColumn('artist');
                        }
                      )}
                    </tr>
                  </thead>
                  <tbody>
                    {sortedTracks.map((row: any) => (
                      <tr>
                        <td align="center">
                          {!this.state.trackSelection.find(
                            (track: any) => track && row && track.id === row.id
                          ) ? (
                            <AddIcon
                              className={`selection-button-icon has-text-link ${
                                this.state.disableTable ? 'disabled-icon' : ''
                              }`}
                              onClick={() => {
                                this.addToSelection(row);
                              }}
                            />
                          ) : (
                            <RemoveIcon
                              className={`selection-button-icon has-text-link ${
                                this.state.disableTable ? 'disabled-icon' : ''
                              }`}
                              onClick={() => {
                                this.removeFromSelection(row);
                              }}
                            />
                          )}
                        </td>
                        <td>{row.name}</td>
                        <td>{row.key}</td>
                        <td>{row.mode}</td>
                        <td>{Number(row.tempo)}</td>
                        <td>{row.artist}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        ) : (
          <p className="notification is-danger">
            Error Loading Tracks, Refresh the Page
          </p>
        )}
      </div>
    );
  }
}

function mapStateToProps(state: any) {
  return {};
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    addFlashMessage: (message: any) =>
      dispatch({ type: ADD_FLASH_MESSAGE, payload: message }),
    addPendingRequest: (request: any) =>
      dispatch({ type: ADD_PENDING_REQUEST, payload: request }),
    finishPendingRequest: (id: any) =>
      dispatch({ type: FINISH_PENDING_REQUEST, payload: id })
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Table);
