import {
  IS_LOADING,
  ADD_PENDING_REQUEST,
  FINISH_PENDING_REQUEST
} from '../actions/index.actions';

export interface AjaxState {
  isLoading: boolean;
  requests: any[];
}

const initialState = { isLoading: false, requests: [] };
export default function ajaxReducer(
  state = initialState,
  action: { type: string; payload: any }
) {
  switch (action.type) {
    case ADD_PENDING_REQUEST:
      return { ...state, requests: [...state.requests, action.payload] };
    case FINISH_PENDING_REQUEST:
      return {
        ...state,
        requests: [...state.requests].filter(
          (request: any) => !!request.id === action.payload
        )
      };
    case IS_LOADING:
      return { ...state, isLoading: action.payload };
    default:
      return state;
  }
}
