import React, { Component } from 'react';
import { connect } from 'react-redux';
import { GlobalState } from './reducers/index.reducer';
import { DELETE_FLASH_MESSAGE } from './actions/index.actions';
import { FLASH_MESSAGE_TYPES } from './constants/constants';
interface FlashMessagesProps {
  flashMessages: any[];
  deleteFlashMessage: Function;
}
interface FlashMessagesState {
  message: any;
}

class FlashMessages extends Component<FlashMessagesProps, FlashMessagesState> {
  constructor(props: FlashMessagesProps) {
    super(props);
    this.state = {
      message: null
    };
  }

  componentDidUpdate(previousProps: any) {
    const previousLastMessage =
      previousProps.flashMessages[previousProps.flashMessages.length - 1];
    const lastMessage = this.props.flashMessages[
      this.props.flashMessages.length - 1
    ];
    if (
      lastMessage &&
      previousLastMessage &&
      lastMessage.id !== previousLastMessage.id
    ) {
      this.renderFlashMessage();
      return;
    }

    if (!previousLastMessage && lastMessage) {
      this.renderFlashMessage();
    }
  }

  renderFlashMessage() {
    const message = this.props.flashMessages[
      this.props.flashMessages.length - 1
    ];
    if (!message) return;
    setTimeout(() => {
      this.props.deleteFlashMessage(message.id);
    }, 5000);
  }

  render() {
    const { flashMessages } = this.props;
    const message = flashMessages[flashMessages.length - 1];
    return (
      <div className="flash-messages-wrapper">
        {message ? (
          <div className="flash-message-content">
            {message.type === FLASH_MESSAGE_TYPES.SUCCESS ? (
              <div className="notification is-success">{message.text}</div>
            ) : null}

            {message.type === FLASH_MESSAGE_TYPES.ERROR ? (
              <div className="notification is-danger">{message.text}</div>
            ) : null}
          </div>
        ) : null}
      </div>
    );
  }
}

const mapDispatchToProps = (dispatch: any) => {
  return {
    deleteFlashMessage: (id: string) =>
      dispatch({ type: DELETE_FLASH_MESSAGE, payload: id })
  };
};

const mapStateToProps = (state: GlobalState) => {
  return {
    flashMessages: state.flashMessages
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(FlashMessages);
