import { DEAUTHENTICATE, AUTHENTICATE } from '../actions/index.actions';

export interface AuthState {
  isAuthenticated: boolean;
  id: string | null;
}

const initialState = { isAuthenticated: false, id: null };
export default function authReducer(
  state = initialState,
  action: { type: string; payload: string }
) {
  switch (action.type) {
    case AUTHENTICATE:
      return { ...state, isAuthenticated: true, id: action.payload };
    case DEAUTHENTICATE:
      return { ...state, isAuthenticated: false, id: null };
    default:
      return state;
  }
}
