import './App.css';

import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware, combineReducers } from 'redux';
import thunkMiddleware from 'redux-thunk';
import { composeWithDevTools } from 'redux-devtools-extension';
import 'bulma/css/bulma.css';

import reducer from './reducers/index.reducer';

import FlashMessages from './FlashMessages';
import Navbar from './components/navbar/Navbar';
import Home from './components/home/Home';
import AlbumsAndPlaylists from './components/albumsAndPlaylists/AlbumsAndPlaylists';
import authRequired from './components/auth/RequireAuth';
import TracksTable from './components/tracks/TracksTable';
import SelectionTable from './components/userSelection/UserSelection';
import AjaxSpinner from './AjaxSpinner';

const store = createStore(
  combineReducers(reducer),
  composeWithDevTools(
    applyMiddleware(thunkMiddleware)
    // other store enhancers if any
  )
);

const renderFlashMessages = () => {};

const App: React.FC = () => {
  return (
    <Provider store={store}>
      <Router>
        <div>
          <AjaxSpinner />
          <FlashMessages />
          <Navbar />
          <Switch>
            <Route path="/search"></Route>
            <Route
              path="/your-playlists"
              component={authRequired(AlbumsAndPlaylists, {
                type: 'PLAYLISTS'
              })}
            ></Route>
            <Route
              path="/your-albums"
              component={authRequired(AlbumsAndPlaylists, {
                type: 'ALBUMS'
              })}
            ></Route>
            <Route path="/tracks" component={authRequired(TracksTable)}></Route>
            <Route
              path="/selection"
              component={authRequired(SelectionTable)}
            ></Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
        </div>
      </Router>
    </Provider>
  );
};

export default App;
